.skyplus-design-system-country-dropdown-wrapper {
    width: auto;
    position: relative;
    height: auto;
    justify-content: center;
    cursor: pointer;

    .selected-country-box-wrapper {
        border-radius: 8px;
        border: 1px solid  #E2EBF2;
        background: #fff;
        justify-content: space-between;
        padding: 12px 8px;
        color: #25304B;
        font-size: 14px;
        font-weight: 300;
        max-height: 44px;
        width: max-content;
    }

}